<template>
  <el-dialog title="添加商品" :visible.sync="categoryAddGoodsCmpVisible" width="450px" :before-close="addCateDialogClosed">
    <!--添加分类表单区域-->
    <el-form ref="addCateFormRef" :model="selectData" :rules="addCateFormRules" size="small" label-width="90px">
      <el-form-item label="分类:">
        <span>{{ selectData.name }}</span>
      </el-form-item>
      <el-form-item label="商品:" prop="prodId">
        <el-select v-model="selectData.prodId" filterable multiple placeholder="请选择">
          <el-option
            v-for="item in goodslist"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <!--底部按钮区域-->
    <span slot="footer" class="dialog-footer">
      <el-button @click="addCateDialogClosed">取 消</el-button>
      <el-button type="primary" @click="addCate">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'categoryAddGoodsCmp',

  props: {
    categoryAddGoodsCmpVisible: {
      type: Boolean,
      default: false,
    },
    selectData: {
      type: Object,
      default: () => {
        return {
          prodId: [],
        }
      }
    }
  },

  data () {
    return {
      // 添加分类的表单校验
      addCateFormRules: {
        prodId: [
          { required: true, message: '请选择商品', trigger: 'change' }
        ],
      },
      goodslist: [],
    }
  },

  async created () {
    await this.apiProductQuery()
  },

  methods: {
    async addCate () {
      await this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const data = {
          shopClassificationId: this.selectData.id,
          prodId: this.selectData.prodId.join(',')
        }
        const res = await this.$http.post(this.$api.addShopClassificationProds, data)
        if (res.data.success) {
          this.$message.success('保存成功')
          this.addCateDialogClosed()
          this.reload()
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    addCateDialogClosed () {
      this.$emit('close')
    },
    
    // 查询商品列表
    async apiProductQuery () {
      const data = {
        state: 1,
        pageSize: 9999,
      }
      const res = await this.$http.post(this.$api.productQuery, data)
      this.goodslist = res.data.data.list
    }
  },

  inject: ['reload']
}
</script>

<style lang="scss" scoped>

</style>
