<template>
  <div>
    <layout-box one="商品管理" two="商品分类">
      <el-card class="mb-2" shadow="never">
        <div class="d-flex">
          <areaSelectCmp2 :areaParent="search.area" size="mini" @childFn2="onChildFn2($event)"></areaSelectCmp2>
          <template v-if="roleCode === '003' || roleCode === '005'">
            <el-button class="btn-add" size="mini" @click="addShopCategory">添加店铺分类</el-button>
          </template>
          <template v-else>
            <el-button
              class="btn-add"
              size="mini"
              @click="showAddCateDialog()"
            >
              添加分类
            </el-button>
          </template>
        </div>
      </el-card>

      <!--分类列表区域-->
      <tree-table
        :data="cateList"
        :columns="roleCode === '003' || roleCode === '005' ? columns2 : columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
        stripe
      >
        <template slot="operate" slot-scope="scope">
          <div class="">
            <template v-if="roleCode === '003' || roleCode === '005'">
              <el-button size="mini" @click="categoryAddGoods(scope)">在分类中添加商品</el-button>
            </template>
            <el-button size="mini" icon="el-icon-edit" @click="editCateDialog(scope)">编辑</el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="delCateDialog(scope.row.id)">删除</el-button>
          </div>
        </template>
        <template slot="log" slot-scope="scope">
          <el-image :src="scope.row.log" fit="cover" style="width: 100px"></el-image>
        </template>
        <template slot="level" slot-scope="scope">
          <el-tag v-if="scope.row.level == 0">一级</el-tag>
          <el-tag v-else-if="scope.row.level == 1" type="success">二级</el-tag>
          <el-tag v-else type="warning">三级</el-tag>
        </template>
      </tree-table>
      <el-pagination
        class="mt-1"
        :current-page="queryInfo.pageNo"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>

      <!--添加分类的对话框-->
      <el-dialog title="添加分类" :visible.sync="addCateDialogVisible" width="450px" @close="addCateDialogClosed">
        <!--添加分类表单区域-->
        <el-form ref="addCateFormRef" :model="addCateForm" :rules="addCateFormRules" label-width="90px">
          <el-form-item label="分类名称" prop="categoryName">
            <el-input v-model="addCateForm.categoryName" placeholder="请输入分类名称"></el-input>
          </el-form-item>

          <el-form-item label="父级分类" prop="selectedKeys">
            <el-cascader
              ref="cascaderRef"
              v-model="selectedKeys"
              :options="parentCateList"
              :props="cascaderProps"
              clearable
            ></el-cascader>
          </el-form-item>
          <el-form-item label="级别" prop="level">
            <el-select v-model="addCateForm.level" placeholder="请选择">
              <el-option label="一级" value="0"></el-option>
              <el-option label="二级" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="排序" prop="rank">
            <el-input v-model="addCateForm.rank" placeholder="请输入排序"></el-input>
          </el-form-item>
          <el-form-item label="分类图标" prop="log" required>
            <el-upload class="avatar-uploader" action="http-request" :http-request="onUploadFile" :show-file-list="false">
              <el-image v-if="addCateForm.log" :src="addCateForm.log" fit="contain" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon" />
            </el-upload>
          </el-form-item>
        </el-form>
        <!--底部按钮区域-->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addCateDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addCate">确 定</el-button>
        </span>
      </el-dialog>
      <!--           编辑 分类-->
      <el-dialog
        title="编辑分类"
        :visible.sync="editDialogVisible"
        width="450px"
        @close="editDialogClosed"
      >
        <!--编辑表单区域-->
        <el-form
          ref="editFormRef"
          :model="eddCateForm"
          :rules="eddCateFormRules"
          label-width="90px"
        >
          <el-form-item label="分类名称" prop="categoryName">
            <el-input v-model="eddCateForm.categoryName"></el-input>
          </el-form-item>
          <el-form-item label="父级分类">
            <el-select v-model="eddCateForm.parentId" placeholder="请选择分类">
              <el-option
                v-for="item in parentCateList"
                :key="item.id"
                :label="item.categoryName"
                :value="item.id"
              >
              </el-option>
            </el-select>

          </el-form-item>
          <el-form-item label="级别" prop="level">
            <el-select v-model="eddCateForm.level" placeholder="请选择">
              <el-option label="一级" value="0"></el-option>
              <el-option label="二级" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="排序" prop="rank">
            <el-input v-model="eddCateForm.rank" placeholder="请输入排序"></el-input>
          </el-form-item>
          <el-form-item label="分类图标" prop="log" required>
            <el-upload class="avatar-uploader" action="http-request" :http-request="onUploadFileEdd" :show-file-list="false">
              <el-image v-if="eddCateForm.log" :src="eddCateForm.log" fit="contain" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon" />
            </el-upload>
          </el-form-item>
        </el-form>
        <!--底部按钮区域-->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editCateInfo">确 定</el-button>
        </span>
      </el-dialog>
    </layout-box>

    <add-shop-category-cmp :area="search.area" :addShopCategoryCmpVisible="addShopCategoryCmpVisible" @close="addShopCategoryCmpClose" />
    <edit-shop-category-cmp :area="search.area" :editShopCategoryCmpVisible="editShopCategoryCmpVisible" :selectData="selectData" @close="editShopCategoryCmpClose" />
    <category-add-goods-cmp :categoryAddGoodsCmpVisible="categoryAddGoodsCmpVisible" :selectData="selectData" @close="categoryAddGoodsCmpClose" />
  </div>
</template>

<script>
import areaSelectCmp2 from '@/components/area_select_cmp/index2.0.vue'
import editShopCategoryCmp from './cmp/edit_shop_category.vue'
import addShopCategoryCmp from './cmp/add_shop_category.vue'
import categoryAddGoodsCmp from './cmp/category_add_goods.vue'
import { MIXINS_UPLOADFILE_EVNET } from '@/mixins/event.js'

export default {
  data() {
    return {
      editDialogVisible: false, // 控制编辑分类的对话框是否显示
      addCateDialogVisible: false,
      // // 指定级联选择器的配置对象
      cascaderProps: {
        expandTrigger: 'hover', // 次级菜单的展开方式 click / hover
        checkStrictly: true, // 允许选择任意一级的选项
        value: 'id', // 指定选中值的属性
        label: 'categoryName', // 指定选中标签的名称
        children: 'tress' // 指定父子嵌套的属性
      },
      addCateForm: {
        id: null,
        platformType: 0,
        categoryName: '', // 将要添加的分类名称
        parentId: null, // 父分类的ID
        categoryCode: null,
        level: null,
        log: '', // 图标
      },
      eddCateForm: {
        platformType: 0,
        id: null,
        categoryName: '', // 将要添加的分类名称
        parentId: null, // 父分类的ID
        categoryCode: null,
        level: null
      },
      // 添加分类的表单校验
      addCateFormRules: {
        categoryName: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ],
        level: [{ required: true, message: '请输入商品级别', trigger: 'change' }],
        platformType: [{ required: true, message: '请输入商品类型', trigger: 'change' }],
        selectedKeys: [{ required: false, message: '请输入父级分类', trigger: 'change' }],
        rank: [{ required: true, message: '请输入分类排序', trigger: 'change' }],
      },
      // eslint-disable-next-line no-mixed-spaces-and-tabs
	    eddCateFormRules: {
        categoryName: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ],
        level: [{ required: true, message: '请输入商品级别', trigger: 'change' }],
        platformType: [{ required: true, message: '请输入商品类型', trigger: 'change' }],
        // eslint-disable-next-line no-mixed-spaces-and-tabs
        rank: [{ required: true, message: '请输入分类排序', trigger: 'change' }],
      },
      cateList: [],
      // 为table表格各列的配置定义
      columns: [
        {
          label: '排序', // 列标题名称
          prop: 'rank', // 对应列内容的属性名
          width: 'auto',
        },
        {
          label: '分类名称', // 列标题名称
          prop: 'categoryName', // 对应列内容的属性名
          width: 'auto',
        },
        {
          label: '创建时间',
          prop: 'createTime',
          width: 'auto',
        },
        {
          label: '分类图标',
          type: 'template', // 表示：把当前列定义为模板列
          template: 'log', // 表示当前这列使用的模板名称
          width: '120',
        },
        {
          label: '级别',
          type: 'template', // 表示：把当前列定义为模板列
          template: 'level', // 表示当前这列使用的模板名称
          width: 'auto',
        },
        {
          label: '操作',
          type: 'template', // 表示：把当前列定义为模板列
          template: 'operate', // 表示当前这列使用的模板名称
          width: '200',
          align: 'center'
        }
      ],
      columns2: [
        {
          label: '排序', // 列标题名称
          prop: 'rank', // 对应列内容的属性名
          width: 'auto',
        },
        {
          label: '分类名称', // 列标题名称
          prop: 'categoryName', // 对应列内容的属性名
          width: 'auto',
        },
        {
          label: '创建时间',
          prop: 'createTime',
          width: 'auto',
        },
        {
          label: '操作',
          type: 'template', // 表示：把当前列定义为模板列
          template: 'operate', // 表示当前这列使用的模板名称
          width: '319',
          align: 'center'
        }
      ],
      total: 0,
      queryInfo: {
        pageNo: 1,
        pageSize: 10,
        platformType: null,
      },
      // parentId: 0,
      parentCateList: [], // 父级分类列表数据
      // 选中的父级分类的ID数组
      selectedKeys: [],
      search: {
        area: '江苏省,苏州市,吴江区',
      },

      // ---------------------------------------
      addShopCategoryCmpVisible: false, // 添加店铺的分类
      roleCode: localStorage.getItem('roleCode'), // 角色权限编号
      editShopCategoryCmpVisible: false, // 编辑店铺的分类
      selectData: {}, // 编辑时选择的分类
      categoryAddGoodsCmpVisible: false, // 在分类中添加商品窗口
    }
  },

  components: {
    areaSelectCmp2,
    addShopCategoryCmp,
    editShopCategoryCmp,
    categoryAddGoodsCmp,
  },

  mixins: [
    MIXINS_UPLOADFILE_EVNET,
  ],

  methods: {
    init () {
      this.fetch()
      if (this.roleCode !== '003' && this.roleCode !== '005') {
        this.getParentCateList()
      }
    },
    handleSizeChange(pageSize) {
      this.queryInfo.pageSize = pageSize
      this.fetch()
    },
    handleCurrentChange(pageNo) {
      this.queryInfo.pageNo = pageNo
      this.fetch()
    },
    // 分页详情
    async fetch() {
      let data = {
        ...this.queryInfo,
        ...this.search
      }
      const res = await this.$http.post('/woho/product/category/query', data)
      if (res.data.success) {
        this.cateList = res.data.data.list
        this.total = res.data.data.total
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 获取父类ID
    async getParentCateList() {
      const res = await this.$http.get(`/woho/product/category/findByTree/${this.search.area}`)
      this.parentCateList = res.data.data
      this.parentCateList.unshift({ id: '0', categoryName: '无上级分类' })
    },
    // 点击按钮 弹出添加分类对话框
    showAddCateDialog() {
      this.addCateDialogVisible = true
      // this.parentCateList()
    },
    parentCateChanged() {
      if (this.selectedKeys.length > 0) {
        // 选择最后一项当作父分类ID赋值
        this.addCateForm.parentId = this.selectedKeys[
          this.selectedKeys.length - 1
        ]
        // 为当前要添加的分类的等级赋值
        this.addCateForm.length = this.selectedKeys.length
        return
      } else {
        // 父分类ID赋值
        this.addCateForm.parentId = null
        // 为当前要添加的分类的等级赋值
        this.addCateForm.level = null
      }
      console.log(this.addCateForm)
    },
    // 监听 添加分类对话框的关闭事件
    addCateDialogClosed() {
      // 表单内容重置为空
      this.$refs.addCateFormRef.resetFields() // 通过ref引用调用resetFields方法
      // // 选中的父级分类的ID数组 重置为空
      this.selectedKeys = []
      // // 父分类id 和 当前分类等级 重置为空
      this.addCateForm.parentId = null
      this.addCateForm.level = null
    },
    // 点击按钮，添加新的分类
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        this.addCateForm.parentId = this.selectedKeys[0]
        this.addCateForm.area = this.search.area
        const { data: res } = await this.$http.post(
          '/woho/product/category/insert',
          this.addCateForm
        )
        if (!res.success) return this.$message.error(res.msg)
        this.$message.success('添加商品分类成功！')
        this.addCateDialogVisible = false
        this.init()
      })
    },
    // 删除分类
    async delCateDialog(id) {
      // 弹框 询问用户是否删除
      const confirmResult = await this.$confirm(
        '此操作将永久删除该分类, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      // 如果用户确认删除，则返回值为字符串 confirm
      // 如果用户取消删除，则返回值为字符串 cancel
      // console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      let res = '' 
      if (this.roleCode === '003' || this.roleCode === '005') {
      // 店铺删除
        res = await this.$http.delete(
          '/woho/product/category/delete/' + id + '/' + 1
        )
      } else {
      // 平台删除
        res = await this.$http.delete(
          '/woho/product/category/delete/' + id + '/' + 0
        )
      }
      
      if (res.data.code !== 200) {
        return this.$message.error('删除分类失败！')
      }
      this.$message.success('删除分类成功！')
      this.getParentCateList()
      this.fetch()
    },
    // 更新
    async editCateDialog(scope) {
      if (this.roleCode === '003' || this.roleCode === '005') {
      // '店铺和自营店铺'
        const data = {
          shopClassificationId: scope.row.id,
          name: scope.row.categoryName,
          sorting: scope.row.rank
        }
        this.selectData = data
        this.editShopCategoryCmpVisible = true
      } else {
      // '平台'
        const res = await this.$http.post('/woho/product/category/findById/' + scope.row.id)
        this.eddCateForm = res.data
        console.log(this.eddCateForm)
        // this.eddCateForm = res.data.data
        this.editDialogVisible = true
      }
    },
    // 关闭新增店铺分类窗口
    addShopCategoryCmpClose () {
      this.addShopCategoryCmpVisible = false
    },
    // 关闭新增店铺分类窗口
    editShopCategoryCmpClose () {
      this.editShopCategoryCmpVisible = false
    },
    // 关闭分类添加商品窗口
    categoryAddGoodsCmpClose () {
      this.categoryAddGoodsCmpVisible = false
    },
    // 点击添加店铺分类
    addShopCategory () {
      this.addShopCategoryCmpVisible = true
    },
    // 点击在分类中添加商品
    async categoryAddGoods (scope) {
      const data = {
        shopClassificationId: scope.row.id
      }
      const res = await this.$http.post(this.$api.findShopClassifications, data)
      const selectData = res.data.data.list[0]
      const prodId = res.data.data.list[0].productHeadList.map((item) => {
        return item.id
      })
      this.selectData = {
        ...selectData,
        prodId
      }
      this.categoryAddGoodsCmpVisible = true
    },

    // ------------------------------------------------------------------------------
    editDialogClosed() {
      // 表单内容重置为空
      this.$refs.editFormRef.resetFields() // 通过ref引用调用resetFields方法
    },
    async editCateInfo() {
      let data = {
        ...this.eddCateForm,
        ...this.search
      }
      const res = await this.$http.put('/woho/product/category/update', data)
      if (res.data.success) {
        this.$message.success('更新成功')
        this.editDialogVisible = false
        this.init()
      } else {
        this.$message.error(res.data.msg)
      }
    },

    // 选择地区子组件向父组件传值
    onChildFn2(e) {
      this.search.area = e
      this.init()
    },
    // 添加分类的时候的图片上传
    onUploadFile (params) {
      this.uploadFile(params).then(res => {
        this.addCateForm.log = res
      })
    },
    // 编辑分类的时候的图片上传
    onUploadFileEdd (params) {
      this.uploadFile(params).then(res => {
        this.eddCateForm.log = res
      })
    },
  },

  created() {
    this.init()
  },
}
</script>

<style lang="scss" scoped>
.el-cascader {
  width: 100%;
}
.d-flex{
  display: flex;
  justify-content: space-between;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.el-uploader{
  border: 1px dashed #d9d9d9 !important;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.el-icon-plus{
  border: 1px dashed #ccc;
}
</style>
