<template>
  <el-dialog title="添加分类" :visible.sync="addShopCategoryCmpVisible" :close-on-click-modal="false" width="450px" :before-close="addCateDialogClosed">
    <!--添加分类表单区域-->
    <el-form ref="addCateFormRef" :model="addCateForm" :rules="addCateFormRules" size="small" label-width="90px">
      <el-form-item label="分类名称" prop="name">
        <el-input v-model="addCateForm.name" placeholder="请输入分类名称"></el-input>
      </el-form-item>
      <el-form-item label="排序" prop="sorting">
        <el-input v-model="addCateForm.sorting" placeholder="请输入排序"></el-input>
      </el-form-item>
    </el-form>
    <!--底部按钮区域-->
    <span slot="footer" class="dialog-footer">
      <el-button @click="addCateDialogClosed">取 消</el-button>
      <el-button type="primary" @click="addCate">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'addShopCategoryCmp',

  props: {
    addShopCategoryCmpVisible: {
      type: Boolean,
      default: false,
    },
    area: {
      type: String,
      default: '',
    }
  },

  data () {
    return {
      addCateForm: {
        name: '', // 将要添加的分类名称
        sorting: '', // 排序
        region: this.area,
      },
      // 添加分类的表单校验
      addCateFormRules: {
        name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ],
        sorting: [
          { required: true, message: '请输入排序', trigger: 'blur' }
        ]
      },
    }
  },

  methods: {
    async addCate () {
      await this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const data = {
          ...this.addCateForm
        }
        const res = await this.$http.post(this.$api.addShopClassification, data)
        if (res.data.success) {
          this.$message.success('保存成功')
          this.addCateDialogClosed()
          this.reload()
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    addCateDialogClosed () {
      this.$emit('close')
    }
  },

  inject: ['reload']
}
</script>

